import React, {useState, useEffect} from "react";
import {
  Container,
  Grid,
  Typography,
  Button,
  TextField,
  Paper,
  Tooltip,
  Switch,
  FormControl,
  Select,
  MenuItem,
} from "@mui/material/";
import BT1 from "./img/b1.png";
import ImgList from "./ImgList";
import BasiliskImg from "./img/basilisktime.png";
import GiftImg from "./img/gift.png";
import Elevated from "./Elevated";
import "./App.css";

export const AlertPanel = (props) => {
  const [id, setId] = useState("");
  const [text, setText] = useState("");
  const [price, setPrice] = useState("150");
  const [rate, setRate] = useState("0");
  const [reload, setReload] = useState(true);
  const [basilisk, setBasilisk] = useState(false);
  const [giftboost, setGiftboost] = useState(false);
  const [source, setSource] = useState(false);
  const [lang, setLang] = useState("ch");
  const [recall, setRecall] = useState(false);
  const [loading, setLoading] = useState(false);
  const ln = ["ncn", "ntw", "cn", "en", "tw", "jp", "fr", "ko"];
  const testList = [
    {title: "一般訂閱", command: "!testSub", img: "sub"},
    {title: "快速訂閱", command: "!testSubFast", img: "subFast"},
    {title: "稀有訂閱", command: "!testSubRare", img: "subRare"},
    {title: "小奇點", command: "!testBits", img: "cheer"},
    {title: "揪團", command: "!testRaid", img: "raid"},
    {title: "Paypal", command: "!testDonate", img: "donate"},
  ];
  const {client, channel} = props;
  useEffect(() => {
    getSetting();
  }, []);

  const getSetting = async () => {
    await fetch("https://m3ntru-api.vercel.app/api/alert/rekileggy")
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setBasilisk(data.basilisk);
        setGiftboost(data.gift);
        setLang(data.lang);
        setSource(data.source);
        setRate(data.urrate);
        setLoading(true);
      })
      .catch((error) => console.error(error));
  };

  const setSetting = async (key, value) => {
    let headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };
    let settingBody = {twitch: "rekileggy"};
    settingBody[key] = value;
    await fetch("https://m3ntru-api.vercel.app/api/alert/", {
      method: "POST",
      headers,
      body: JSON.stringify(settingBody),
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
      })
      .catch((error) => console.error(error));
  };

  const handleCommonClick = (name) => (event) => {
    client.say(channel, name);
  };

  const handleRecallClick = (name) => (event) => {
    if (id) {
      if (name == "!cheer") {
        if (text) {
          client.say(channel, name + " " + id);
          client.say(channel, text);
        }
      } else {
        client.say(channel, name + " " + id + " " + (price ? price : "1"));
        client.say(channel, text ? text : "0");
      }
    }
  };

  const handleIdChange = () => (event) => {
    var msg = event.target.value;
    setId(msg);
  };

  const handleTextChange = () => (event) => {
    var msg = event.target.value;
    setText(msg);
  };

  const handleNumberChange = () => (event) => {
    var msg = event.target.value;
    setPrice(msg);
  };

  const handleRateChange = () => (event) => {
    var msg = event.target.value;
    setRate(msg);
  };

  const handleSwitchChange = (value) => (event) => {
    const status = event.target.checked ? "on" : "off";
    switch (value) {
      case "basilisk":
        setBasilisk(event.target.checked);
        client.say(channel, "!basilisktime " + status);
        setSetting("basilisk", event.target.checked);
        break;
      case "giftboost":
        setGiftboost(event.target.checked);
        client.say(channel, "!giftboost " + status);
        setSetting("gift", event.target.checked);
        break;
      case "reload":
        setReload(!event.target.checked);
        break;
      default:
        break;
    }
  };

  const handleLangChange = () => (event) => {
    setLang(event.target.value);
    client.say(channel, "!lang " + event.target.value);
    setSetting("lang", event.target.value);
  };

  const handleSourceChange = () => (event) => {
    const result = event.target.value == "Chat";
    const status = result ? "on" : "off";
    setSource(result);
    client.say(channel, "!source " + status);
    setSetting("source", result);
  };

  const handleRateSave = () => (event) => {
    client.say(channel, "!rate " + rate);
    setSetting("urrate", rate);
  };

  return (
    <div className="App">
      <div style={{padding: "7px 0px"}}>
        <Container style={{margin: "7px auto", padding: "15px"}}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={9}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <Container className="grid-container">
                    <Typography
                      variant="subtitle2"
                      component="p"
                      className="block-title"
                    >
                      Alert Test
                    </Typography>
                    <div style={{display: "inline-block"}}>
                      {testList.map((data, index) => (
                        <Tooltip title={data.title} placement="bottom">
                          <div
                            style={{
                              display: "inline-block",
                              justifyContent: "center",
                              margin: "5px 5px",
                            }}
                          >
                            <div
                              className="jb-button"
                              style={{
                                color: "white",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                              }}
                              onClick={handleCommonClick(data.command)}
                            >
                              <div className="jb-button-img">
                                <img src={ImgList[data.img]} alt="logo" />
                              </div>
                            </div>
                          </div>
                        </Tooltip>
                      ))}
                    </div>
                  </Container>
                </Grid>
                <Grid item xs={12}>
                  <Container className="grid-container">
                    <Typography
                      variant="subtitle2"
                      component="p"
                      className="block-title"
                    >
                      Recall
                    </Typography>
                    {/* <Typography variant='subtitle2' component='p' className='block-title'>
                                        Manual <Switch checked={recall} onChange={()=>{setRecall(!recall)}}/> Elevated
                                    </Typography> */}
                    {recall ? (
                      <div style={{width: "100%"}}>
                        <Elevated client={client} channel={channel} />
                        {/* <div className="text-4xl text-white">SSS</div> */}
                      </div>
                    ) : (
                      <div style={{width: "100%"}}>
                        {/* <div className='flex justify-center'>
                                            <RadioGroup row aria-label="gender" name={"Price"} defaultValue={"150"} onChange={handleChange}>
                                                <FormControlLabel value={"150"} control={<Radio />} label={"150"} className='block-title' />
                                                <FormControlLabel value={"300"} control={<Radio />} label={"300"} className='block-title' />
                                                <FormControlLabel value={"750"} control={<Radio />} label={"750"} className='block-title' />
                                                <FormControlLabel value={"1500"} control={<Radio />} label={"1500"} className='block-title' />
                                                <FormControlLabel value={"3000"} control={<Radio />} label={"3000"} className='block-title' />
                                            </RadioGroup>
                                        </div> */}
                        <TextField
                          component={Paper}
                          label="Twitch id"
                          variant="outlined"
                          id="outlined-size-normal"
                          size="small"
                          value={id}
                          style={{width: "85%", margin: "10px 0px"}}
                          onChange={handleIdChange()}
                        />
                        <TextField
                          component={Paper}
                          label="Months / Donation"
                          variant="outlined"
                          minRows={7}
                          id="outlined-size-normal"
                          size="small"
                          value={price}
                          style={{width: "85%", margin: "10px 0px"}}
                          onChange={handleNumberChange()}
                        />
                        <TextField
                          component={Paper}
                          multiline
                          label="Text"
                          variant="outlined"
                          minRows={7}
                          id="outlined-size-normal"
                          size="small"
                          value={text}
                          style={{width: "85%", margin: "0px 0px"}}
                          onChange={handleTextChange()}
                        />
                        <div style={{padding: "15px 0px"}}>
                          <Button
                            variant="contained"
                            color="primary"
                            style={{margin: "4px 10px"}}
                            onClick={handleRecallClick("!sub")}
                          >
                            Sub
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            style={{margin: "4px 10px"}}
                            onClick={handleRecallClick("!cheer")}
                          >
                            Bits
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            style={{margin: "4px 10px"}}
                            onClick={handleRecallClick("!donate")}
                          >
                            Paypal
                          </Button>
                        </div>
                      </div>
                    )}
                  </Container>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={12} md={3}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  {loading ? (
                    <Container className="grid-container">
                      {/* <Grid
                      container
                      spacing={1}
                      className="label-switch"
                      style={{margin: "auto"}}
                    >
                      <Grid
                        className="flex justify-center"
                        style={{margin: "auto"}}
                        item
                        xs={6}
                      >
                        <img
                          title="Basilisk"
                          src={BasiliskImg}
                          alt="logo"
                          style={{height: "40px"}}
                        />
                      </Grid>
                      <Grid item xs={6}>
                        <Switch
                          checked={basilisk}
                          onChange={handleSwitchChange("basilisk")}
                        />
                      </Grid>
                    </Grid> */}
                      <Grid
                        container
                        spacing={1}
                        className="label-switch"
                        style={{margin: "auto"}}
                      >
                        <Grid
                          className="flex justify-center"
                          style={{margin: "auto"}}
                          item
                          xs={6}
                        >
                          <img
                            title="Gift Boost"
                            src={GiftImg}
                            alt="logo"
                            style={{height: "40px"}}
                          />
                        </Grid>
                        <Grid item xs={6}>
                          <Switch
                            color="error"
                            checked={giftboost}
                            onChange={handleSwitchChange("giftboost")}
                          />
                        </Grid>
                      </Grid>
                      <Grid
                        container
                        spacing={1}
                        className="label-switch"
                        style={{margin: "auto"}}
                      >
                        <Grid style={{margin: "auto"}} item xs={6}>
                          🌎
                        </Grid>
                        <Grid item xs={6}>
                          <FormControl size="small">
                            <Select
                              labelId="demo-simple-select-label"
                              style={{
                                backgroundColor: "#999999",
                              }}
                              value={lang}
                              onChange={handleLangChange()}
                            >
                              {ln.map((data, index) => (
                                <MenuItem key={data} value={data}>
                                  {data}
                                </MenuItem>
                              ))}
                              <MenuItem key={"random"} value={"random"}>
                                {"random"}
                              </MenuItem>
                            </Select>
                          </FormControl>
                        </Grid>
                      </Grid>
                      <Grid
                        container
                        spacing={1}
                        className="label-switch"
                        style={{margin: "auto"}}
                      >
                        <Grid
                          style={{margin: "auto"}}
                          className="block-title"
                          item
                          xs={6}
                        >
                          source
                        </Grid>
                        <Grid item xs={6}>
                          <FormControl size="small">
                            <Select
                              labelId="demo-simple-select-label"
                              style={{
                                backgroundColor: "#999999",
                              }}
                              value={source ? "Chat" : "Streamlabs"}
                              onChange={handleSourceChange()}
                            >
                              <MenuItem key={false} value={"Streamlabs"}>
                                SL
                              </MenuItem>
                              <MenuItem key={true} value={"Chat"}>
                                Chat
                              </MenuItem>
                            </Select>
                          </FormControl>
                        </Grid>
                      </Grid>
                      <Grid
                        container
                        spacing={1}
                        className="label-switch"
                        style={{margin: "auto"}}
                      >
                        <Grid
                          style={{margin: "auto"}}
                          className="block-title"
                          item
                          xs={6}
                        >
                          ur %
                        </Grid>
                        <Grid item xs={6}>
                          <FormControl size="small">
                            <TextField
                              component={Paper}
                              variant="outlined"
                              id="outlined-size-normal"
                              size="small"
                              value={rate}
                              type="number"
                              style={{width: "85%"}}
                              onChange={handleRateChange()}
                            />
                          </FormControl>
                        </Grid>
                      </Grid>
                      <Button
                        variant="contained"
                        color="primary"
                        style={{margin: "10px 10px"}}
                        onClick={handleRateSave()}
                      >
                        Save %
                      </Button>
                    </Container>
                  ) : (
                    <Container
                      className="grid-container"
                      style={{color: "#ffffff"}}
                    >
                      Loading...
                    </Container>
                  )}
                </Grid>
                <Grid item xs={12}>
                  <Container className="grid-container">
                    <Switch
                      color="default"
                      onChange={handleSwitchChange("reload")}
                    />
                    <Button
                      disabled={reload}
                      variant="contained"
                      color="secondary"
                      style={{margin: "10px 10px"}}
                      onClick={handleCommonClick("!reload2.0")}
                    >
                      ALERTBOX Reload
                    </Button>
                  </Container>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Container>
        <img src={BT1} alt="logo" style={{display: "none"}} />
      </div>
    </div>
  );
};

export default AlertPanel;
